<style>
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    column-gap: 25px;
    overflow-y: hidden;
  }

  @media (max-width: 400px) {
    .grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      column-gap: 25px;
    }
  }
</style>

<div class="grid">
  <slot />
</div>
